.eachItem{
    
    padding:10px;
    display: flex;
    outline: 0.5px solid #f9f9f9;
    user-select: none;
}
.eachItem:hover{
    background: #f3f3f3;
    transition-duration: 0.1s;
}
.eachProfilePic img{
    border-radius: 50%;
    cursor: pointer;
    border: 1px solid green;
    padding:1px;
    transition-duration: 0.2s;
}
.eachProfilePic img:hover{
    filter: brightness(0.8);
}
.eachMessageContent{
    display: grid;
    width: 100%;
    grid-template-areas: "profileName time"
                         "message time";
}
.eachMessageSenderName{
    text-align: left;
    padding-left: 20px;
    font-weight: 600;
    display: flex;
    align-items: center; 
    top:10px;
    /* outline: 0.5px solid red; */
    grid-area: profileName;
}
.eachMessageText{
    padding-left: 20px;
    text-align: left;
    /* outline: 0.5px solid red; */
    grid-area: message;
}
.eachMessageTime{
    /* outline: 0.5px solid red; */
    grid-area: time;
}