.chatHeaderContainer{
    background: #eaeaea;
    height: 100%;
    border-left:0.5px solid #dedede;
    display: grid;
    grid-template-areas:"chatHeaderProfilePic chatName chatActions"
                        "chatHeaderProfilePic chatName chatActions"
                        "chatHeaderProfilePic chatStatus chatActions";
    grid-template-columns: 10% 70% 20%;
}
.chatHeaderProfilePic{
    grid-area: chatHeaderProfilePic;
    display: grid;
    place-content: center;
}
.chatHeaderProfilePic img{
    border-radius: 50%;
    width: 48px;
}
.chatHeaderProfileName{
    text-align: left;
    grid-area: chatName;
    display: flex;
    margin-top: 10px;
    font-size: 32px;
    text-transform: lowercase;
    align-items: center;
}
.chatHeaderProfileStatus{
    text-align: left;
    grid-area: chatStatus;
}
.chatHeaderProfileActions{
    grid-area: chatActions;
}