.chatWindowContainer{
    display: grid;
    grid-template-areas: "chatContents"
                         "chatSendContainer";
    grid-template-rows: 90% 10%;
    height: 100%;
}
.chatContents{
    grid-area: chatContents;
}
.chatContentEachMessage{
    display: inline-flex;
    width: 100%;
    text-align: center;
}
.chatSendContainer{
    background: #f3f3f3;
    grid-area: chatSendContainer;
    display: grid;
    grid-template-areas: "chatEmoji chatInput chatMicrophone";
    grid-template-columns: 10% 80% 10%;
}
.chatEmojiSelectionButton{
    grid-area: chatEmoji;
    display: grid;
    place-content: center;
}
.chatInputField{
    grid-area: chatInput;
    display: grid;
    place-content: center;
}
.chatInputField > input{
    width: 80%;
    font-size: 28px;
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.chatMicrophoneButton{
    grid-area: chatMicrophone;
    display: grid;
    place-content: center;
    font-size: 32px;
}
.chatMicrophoneButton button{
    font-size: 32px;
    margin-right: 20px;
}

