.profileContainer{
    height: 100%;
    border-top-left-radius: 10px;
    background: #eaeaea;
    padding:2px;
    display: grid;
    grid-template-areas: "profile profileName profileName actions actions" ;
}
.profileImageContainer img{
    border-radius: 50%;
    width: 64px;
    grid-area: profile;
}
.profileName{
    display: flex;
    align-items: center; 
    text-align: left;
    grid-area: profileName;
    font-size: 28px;
}
.rightSection{
    grid-area: actions;
    display: inline-flex;
}
.rightSection > div{
    cursor: pointer;
    display: block;
    width: 100%;
    display: flex;
    align-items: center; 
    text-align: center;

}
.rightSection > div:hover{
    box-shadow: 0 0 10px #d0cfcf;
    background: #d0cfcf;
    color: white;
}