.container{
    background: #2196f3;
}
.artificial{
    
    top:0;
    left:0;
    height: 100vh;
    width: 100vw;
}
.appContainer{
    width: 80%;
    height: 80%;
    margin: 0 auto;
    background: white;
    border-radius: 5px;
    box-shadow: 0 0 10px #065a9c;

    display: grid;
    grid-template-areas: "profile header header header header header header header header header header header "
                         "availablechats chatwindow chatwindow chatwindow chatwindow chatwindow chatwindow chatwindow chatwindow chatwindow chatwindow chatwindow ";
    grid-template-rows: 10% 90%;

}

.profile{
    grid-area: profile;
}
.header{
    grid-area: header;
}
.availablechats{
    outline: thick;
    overflow-y: scroll;
    grid-area: availablechats;
}
.chatwindow{
    grid-area: chatwindow;
}

.titleapp{
    color: white;
    padding:10px;
    font-size: 32px;
    padding-top: 30px;padding-bottom: 30px;
    font-family: Verdana, Geneva, Tahoma, sans-serif
}